import { Button, Stack, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { Fragment, useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getTemplates } from 'redux/slices/CMS/Template'
import { getThemes } from 'redux/slices/CMS/Theme'

export default function Template() {
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const location = useLocation()
	const status = useAppSelector((s) => s.CMS.Template.status)
	const list = useAppSelector((s) => s.CMS.Template.list)
	const dispatch = useAppDispatch()
	const currentPath = useAppSelector((s) => s.UI.currentPath)
	useEffect(() => {
		if (status === 'init') dispatch(getTemplates())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	const themes = useAppSelector((s) => s.CMS.Theme.list)
	const themeStatus = useAppSelector((s) => s.CMS.Theme.status)
	useEffect(() => {
		if (themeStatus === 'init') dispatch(getThemes())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	if (!tokenInfo) return null
	return (
		<Stack gap={1}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					テンプレート一覧
				</Typography>
				<Link to="/cms/template/create">
					<Button
						variant="outlined"
						sx={{
							minWidth: 120,
						}}
					>
						新規作成
					</Button>
				</Link>
			</Stack>
			<DataGrid
				rows={list.filter((f) => f.active)}
				columns={[
					{
						field: 'name',
						headerName: 'テンプレート名',
						flex: 1,
						renderCell: (v) => {
							const editor = currentPath.find((f) => f.path === location.pathname + '/' + v.row.id)
							if (editor) return v.row.name
							return (
								<Fragment key={v.id}>
									<Link to={`/cms/template/${v.row.id}`}>{v.row.name}</Link>
								</Fragment>
							)
						},
					},
					{
						field: 'ver',
						headerName: '有効バージョン',
						width: 120,
						renderCell: (v) => {
							return <Fragment key={v.id}>{v.row.ver}</Fragment>
						},
					},
					{
						field: 'themeId',
						headerName: '使用テーマ',
						flex: 1,
						renderCell: (v) => {
							const theme = themes.find((f) => f.id === v.row.themeId)
							return (
								<Typography variant="caption" mr={1}>
									{theme?.name || v.row.themeId}
								</Typography>
							)
						},
					},
					{
						field: 'status',
						headerName: '編集者',
						flex: 1,
						renderCell: (v) => {
							const editor = currentPath.find((f) => f.path === location.pathname + '/' + v.row.id)

							return (
								<Typography variant="caption" mr={1}>
									{editor?.auth.email}
								</Typography>
							)
						},
					},
				]}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 10 },
					},
				}}
				pageSizeOptions={[10, 20, 30, 50]}
				sx={{
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:hover': {},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				density="compact"
				hideFooterSelectedRowCount
				editMode="row"
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
